<template>
    <div>
        <div class="notes-header">
            <h2>Add note</h2>
            <AddNote @new-note="addNote" />
        </div>        

        <div class="list">
            <Note v-for="todo in todoList" 
                  :key="todo.id" 
                  v-bind:todo="todo" 
                  @open-popup="showModal = true, currentIndex = $event"
            />
        </div>

        <Confirm class="confirm"
            v-bind:action="removeNote"
            v-bind:id="currentIndex"
            @close="showModal = false"
            v-show="showModal"
        />
    </div>
</template>

<script>
import Confirm from "../components/Confirm";
import Note from '../components/Note'
import AddNote from '../components/AddNote'
export default {
    props: ['todos', 'id', 'complited'],
    data() {
        return {
            todoList: [
                {
                    id: 1,
                    title: 'new note',
                    todos: []
                }
            ],
            showModal: false,
            currentIndex: 0
        };
    },
    components: {
        Note, AddNote, Confirm
    },
    mounted() {
        if (localStorage.notes) {
            this.todoList = JSON.parse(localStorage.notes)
        }
        if (this.todos) {
            this.todoList[this.id - 1].todos = this.todos
        }
        this.setId()
    },

    watch: {
        todoList: function () {
            localStorage.setItem('notes', JSON.stringify(this.todoList))
        }
    },

    methods: {
        addNote(note) {
            this.todoList.push(note)
            this.setId()
        },
        setId() {
            this.todoList.forEach((item, i) => {
                item.id = i + 1;
            });
        },
        removeNote(id) {
            this.todoList = this.todoList.filter(t => t.id !== id)
            this.setId()
        }
    }
    
}
</script>

<style scoped>
    .notes-header {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        margin-top: 20px;
    }

    h2 {
        margin: 0;
        margin-bottom: 10px;
        color: #333333;
        opacity: 0.4;
    }

    .list {
        max-width: 940px;
        margin: 0 auto;
        display: flex;
        justify-content: start;
        flex-wrap: wrap;
        margin-top: 60px;
    }

    @media (max-width: 1010px) {
        .list {
            max-width: 640px;
        }
    }

    @media (max-width: 670px) {
        .list {
            flex-direction: column;
            align-items: center;
        }
    }

    
</style>