<template>
    <div class="todos">
        <h2 class="todos-title">{{this.title}}</h2>
        <AddItem @new-item="addItem" />
        <TodoList v-bind:todos="todos" @remove-item="removeItem" />

        <div class="buttons-container">
            <router-link type="button" class="button" :to="{name: 'home', params: {todos: this.todos, id: this.id, complited: this.complited}}">Save</router-link>
            <router-link type="button" class="button" :to="{name: 'home'}">Cancel</router-link>

        </div> 
    </div>
</template>

<script>
import TodoList from "../components/TodoList";
import AddItem from "../components/AddItem";

export default {
    name: "App",
    props: ['id', 'title', 'complited'],
    data() {
        return {
            todos: [

            ]
        };
    },
    mounted() {
        if (localStorage.getItem('notes')) {
            let data = JSON.parse(localStorage.getItem('notes'));
            this.todos = data[this.id - 1].todos;
        }
    },
    components: {
        TodoList,
        AddItem
    },
    methods: {
        removeItem(id) {
            this.todos.splice(id - 1, 1)
            this.setId();
        },
        setId() {
            this.todos.forEach((item, i) => {
                item.id = i + 1;
            });
        },
        addItem(item) {
            this.todos.push(item);
            item.id = this.todos.length;
        }
    }
};
</script>

<style>
    .popup {
        display: none;
    }

    .buttons-container {
        display: flex;
        flex-direction: row;
    }

    .todos {
        margin-top: 20px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: start;
    }

    .todos-title {
        margin-top: 20px;
        margin-bottom: 20px;
    }

    .button {
        position: relative;
        color: #666666;
        text-decoration: none;
        transition: 0.3s ease-in-out;
        text-transform: uppercase;
        border: none;
        cursor: pointer;
        background-color: transparent;
        font-size: 14px;
        font-weight: bold;
    }

    .button:first-child {
        margin-right: 60px;
    }

    .button:hover::after,
    .button:hover::before {
        position: absolute;
        content: '';
        top: 5px;
        width: 10px;
        height: 4px;
        background-color: #666666;
    }

    .button:hover::after {
        left: -20px;
    }
    .button:hover::before {
        right: -20px;
    }

    .close {
        position: absolute;
        top: 5px;
        right: 5px;
        width: 25px;
        height: 25px;
        cursor: pointer;
        border: none;
        background-color: transparent;
        color: brown;
        font-size: 20px;
    }
    
</style>