<template>
    <div>
        <ul>
            <TodoItem
                v-for="todo in todos" :key="todo.id"
                v-bind:todo="todo"
                @remove-item="removeItem"
                @open-popup="showModal = true, currentIndex = $event"
             />
        </ul>

        <Confirm class="confirm"
            v-bind:action="removeItem"
            v-bind:id="currentIndex"
            @close="showModal = false"
            v-show="showModal"
        />
    </div>

    
</template>

<script>
import TodoItem from './TodoItem';
import Confirm from "../components/Confirm";
export default {
    props: ['todos'],
    data() {
        return {
            currentIndex: 0,
            showModal: false
        }
    },
    components: {
        TodoItem, Confirm
    },
    methods: {
        removeItem(id) {
            this.$emit('remove-item', id)           
        }
    }
}
</script>

<style scoped>
    ul {
        margin-top: 60px;
        margin-bottom: 20px;
        padding: 0;
        list-style: none;
        display: flex;
        flex-direction: column;
        align-items: center;
        
    }
</style>