<template>
    <div class="item">
        <h3 class="item-title">{{todo.title}}</h3>
        <button class="close" @click="$emit('open-popup', todo.id)">&#10006;</button>
        <ul>
            <li v-for="todo in todo.todos.slice(0, 4)" :key="todo.id"
                v-bind:todo="todo"
                >
                <span>{{todo.id}}:</span>
                <span class="todo-name" v-bind:class="{done: todo.complited}">{{todo.title}}</span>
            </li>
        </ul>
        <router-link class="button" :to="{name: 'todos', params: {id: todo.id, title: todo.title}}">Edit</router-link>


    </div>
</template>

<script>
export default {
    props: {
        todo: {
            type: Object,
            required: true
        }
    }    
}
</script>

