import Vue from 'vue';
import Router from 'vue-router';
import Home from './views/Home';
import Todos from './views/Todos';

Vue.use(Router);

export default new Router({
    mode: 'history',
    base: /vue-todo/,
    routes: [
        {
            path: '/',
            name: 'home',
            component: Home,
            props: true
        },
        {
            path: '/todos/:id',
            name: 'todos',
            component: Todos,
            props: true
        }
    ]
});