<template>
    <form class="form" @submit.prevent="onSubmit">
        <input class="text-input" type="text" v-model="title" maxlength="18" placeholder="Name">
        <button class="button" type="submit">Add</button>
    </form>
</template>

<script>
export default {
    data() {
        return {
            title: ''
        }
    },
    methods: {
        onSubmit() {
            if (this.title !== '') {
                const note = {
                    id: 0,
                    title: this.title,
                    todos: []
                }
                this.$emit('new-note', note)
                this.title = ''
            }
        }
    }
}
</script>

<style scoped>
    .form {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .button {
        margin-top: 20px;
    }
    .text-input {
        border: none;
        border-bottom: 2px solid #666666;
        padding: 10px;
        font-size: 18px;
    }

</style>