            <button type="button" class="button yes-btn" @click="action(id), $emit('close')">Yes</button>
            <button type="button" class="button no-btn" @click="$emit('close')">No</button>
        </div>
    </div>
</template>

<script>
export default {
    props: ['id', 'action']
}
</script>

.<style scoped>
    .popup {
        display: flex;
