<template>
    <form class="form" @submit.prevent="onSubmit">
        <input class="text-input" type="text" v-model="title" maxlength="20">
        <button class="add" type="submit">&#10148;</button>
    </form>
</template>

<script>
export default {
    data() {
        return {
            title: ''
        }
    },
    methods: {
        onSubmit() {
            if (this.title !== '') {
                const newItem = {
                    id: '',
                    title: this.title,
                    complited: false
                }
                this.$emit('new-item', newItem)
                this.title = ''
            }
        }
    }
}
</script>

<style scoped>
    .form {
        display: flex;
        flex-direction: row;
    }
    .text-input {
        border: none;
        border-bottom: 2px solid #666666;
        margin-right: 20px;
        font-size: 18px;
    }
    .add {
        font-size: 30px;
        font-weight: bold;
        background: transparent;
        border: none;
        cursor: pointer;
        color: #3CB371;
    }

    .add:hover {
        color: #2E8B57;
        transition: 0.3s ease-in-out;
    }
</style>