<template>
    <li>
        <div class="name-container" >
            <span>{{todo.id}}:</span>
            <div class="todo-title">
                <span class="todo-name" v-bind:class="{done: todo.complited}" v-on:click="editName">{{todo.title}}</span>
                <input class="edit-input hide" type="text" v-model="todo.title" maxlength="25">
            </div>
        </div>
        <div class="buttons-container">
            <button class="complite-btn" @click="todo.complited = !todo.complited">&#10004;</button>
            <button class="close" @click="$emit('open-popup', todo.id)">&#10006;</button>
        </div>
        
              
        
    </li>
</template>

<script>

export default {
    props: {
        todo: {
            type: Object,
            required: true
        }
    },

    methods: {
        editName() {
            let input = document.querySelectorAll('.edit-input')[this.todo.id - 1],
                name = document.querySelectorAll('.todo-name')[this.todo.id - 1];

            if (!name.classList.contains('done')) {
                name.classList.add('hide');
                input.classList.remove('hide');
                input.focus();

                input.addEventListener('blur', () => {
                    name.classList.remove('hide');
                    input.classList.add('hide');
                });
            }            
        }        
    }
}
</script>

<style scoped>
    
    .hide {
        display: none;
    }

    .edit-input {
        font-size: 16px;
        border: none;
        padding: 5px;
    }
    .todo-name {
        max-width: 250px;
    }
    .buttons-container {
        display: flex;
    }
    .complite-btn {
        margin-right: 20px;
        width: 25px;
        font-size: 18px;
        color: green;
        background-color: transparent;
        border: none;
        cursor: pointer;
    }
    .close {
        position: relative;
        top: 0;
    }
    li {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 400px;
        padding: 10px;
        margin-bottom: 10px;

        border-bottom: 2px solid #a5a5a5;
     
    }    

    .done {
        text-decoration: line-through;
        opacity: 0.6;
    }

    div {
        display: flex;
        align-items: center;        
    }

    span {
        font-weight: bold;
        margin-right: 10px;
        margin-left: 10px;
    }

    @media (max-width: 410px) {
        li {
            width: 280px;
            flex-direction: column;
        }

        .buttons-container {
            margin-top: 20px;
        }

        .name-container {
            align-self: start;
        }
    }

</style>